/* Estilos Globales - Configura el comportamiento b�sico de todos los elementos */
* {
    margin: 0; /* Elimina los m�rgenes externos de todos los elementos */
    padding: 0; /* Elimina el relleno interno de todos los elementos */
    box-sizing: border-box; /* Asegura que los bordes y el relleno se incluyan en el c�lculo del ancho y alto */
}

body {
    font-family: Arial, sans-serif; /* Establece la fuente base para todo el sitio */
    background: #f2e6ff; /* Fondo con tono lila claro */
    display: flex; /* Utiliza el modelo de dise�o flexbox */
    flex-direction: column; /* Organiza los elementos en una columna (de arriba a abajo) */
    align-items: center; /* Centra los elementos horizontalmente */
    text-align: center; /* Centra el texto */
    color: #333333; /* Color de texto gris oscuro */
    min-height: 100vh; /* Asegura que el body ocupe toda la altura visible */
    overflow-x: hidden; /* Evita el desbordamiento horizontal */
}

/* Encabezado principal - H1 */
h1 {
    font-size: 2.625rem; /* Tama�o de fuente m�s peque�o para el t�tulo principal */
    color: #4b0082; /* Tono lila oscuro para el t�tulo */
    margin-top: 30px; /* A�ade un margen superior para separaci�n */
    margin-bottom: 40px; /* A�ade un margen inferior */
