/* Contenedor para el men� de navegaci�n */
nav {
    width: 100%; /* El men� ocupa todo el ancho disponible */
    display: flex; /* Flexbox para la disposici�n de los elementos */
    flex-direction: column; /* Los elementos se disponen verticalmente */
    align-items: center; /* Centra los elementos horizontalmente */
    gap: 75px; /* Espaciado entre los elementos del men� */
