/* Estilizaci�n del men� de botones */
ul.menu {
    list-style: none; /* Elimina los puntos de la lista */
    padding: 0; /* Elimina el relleno de la lista */
    width: 90%; /* El men� ocupa el 90% del ancho disponible */
}

ul.menu li {
    display: block; /* Cada �tem ocupa el 100% de su ancho disponible */
    width: 100%; /* Asegura que los �tems se alineen correctamente */
    text-align: center; /* Centra el texto del �tem */
}

ul.menu li a {
    background-color: #6a4c9c; /* Fondo lila medio para los botones */
    color: #ffffff; /* Texto blanco para los botones */
    text-decoration: none; /* Elimina el subrayado del enlace */
    padding: 15px 30px; /* Relleno interno del bot�n */
    border-radius: 5px; /* Bordes redondeados */
    font-size: 1.275rem; /* Tama�o de la fuente del bot�n */
    font-weight: bold; /* Texto en negrita */
    box-shadow: 0 4.5px 9px rgba(0, 0, 0, 0.2); /* Sombra suave */
    transition: 0.3s ease all; /* Transici�n suave para efectos */
    display: inline-block; /* Hace que el enlace se vea como un bot�n */
    width: 70%; /* El ancho del bot�n es un porcentaje del contenedor */
    max-width: 400px; /* Tama�o m�ximo del bot�n */
    margin-bottom: 22.5px; /* Espaciado inferior entre los botones */
}

ul.menu li a:hover {
    background-color: #5b3c80; /* Fondo m�s oscuro de lila cuando el mouse pasa */
    transform: scale(1.05); /* Aumenta ligeramente el tama�o al hacer hover */
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3); /* Sombra m�s intensa */
}

/* Bot�n de volver fijo en la esquina inferior */
a.volver {
    position: fixed; /* Fija el bot�n en la pantalla */
    bottom: 15px; /* Espaciado desde la parte inferior de la pantalla */
    right: 15px; /* Espaciado desde la derecha */
    background-color: #ffffff; /* Fondo blanco para el bot�n */
    color: #6a4c9c; /* Texto lila oscuro para el bot�n */
    padding: 9px 13.5px; /* Relleno del bot�n */
    border-radius: 12px; /* Bordes redondeados */
    font-size: 0.9rem; /* Tama�o de la fuente */
    font-weight: bold; /* Texto en negrita */
    text-decoration: none; /* Elimina subrayado */
    box-shadow: 0px 3px 7.5px rgba(0, 0, 0, 0.4); /* Sombra suave */
    transition: transform 0.3s ease, background-color 0.3s ease; /* Animaci�n al pasar el mouse */
}

a.volver:hover {
    background-color: #e0b3ff; /* Fondo m�s claro al pasar el mouse */
    transform: scale(1.1); /* Efecto de agrandar ligeramente el bot�n */
}

/* Secci�n inferior */
section {
    margin-top: 40px; /* Margen superior para separar la secci�n */
